<template>
  <v-container class="fill-height" fluid>
    <v-row>
      <v-col cols="12">
        <v-row justify="center">        
          <v-card
            outlined
            width="350"
            class="elevation-20 rounded-lg"
          >
          <v-alert type="error" v-text="pesan" v-show="pesan.length != 0"/>
          <v-progress-linear
            color="red darken-4"
            class="rounded-t-lg"
            indeterminate
            height="20"
            v-show="loadingpersen"
          ></v-progress-linear>
            <v-layout justify-center>
              <v-list>
                <v-list-item class="px-2 mx-4 mb-n3 mt-4">
                  <v-list-item-content>
                    <v-img
                      max-width="50"
                      max-height="150px"
                      src="../assets/LogoTrinity.png"
                    ></v-img>
                  </v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-content>
                    <v-list-item-title class="title">
                      <label>Masuk</label>
                    </v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
            </v-layout>

            <v-card-text class="mt-n10">
              <v-col
                cols="12"
                sm="12"
                md="12"
              >
                <v-subheader class="mx-n4 body-1"><label>Nama</label></v-subheader>
                <v-text-field
                  dense
                  outlined
                  color="red darken-4"
                  class="form-control rounded-lg mt-n2"
                  v-model="credentials.Nama"
                ></v-text-field>
              </v-col>

              <v-col
                cols="12"
                sm="12"
                md="12"
              >
                <v-subheader class="mx-n4 mt-n10 body-1"><label>Kata Sandi</label></v-subheader>
                <v-text-field
                  dense
                  outlined
                  color="red darken-4"
                  class="form-control rounded-lg mt-n2"
                  v-model="credentials.password"
                  :append-icon="TampilkanSandi ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="TampilkanSandi ? 'text' : 'password'"
                  @click:append="TampilkanSandi = !TampilkanSandi"
                  @keyup.enter="login()"
                ></v-text-field>
              </v-col>

              <v-col>
                <v-btn
                  block
                  dark
                  color="red darken-4"
                  class="btn btn-primary rounded-lg text-capitalize mt-n6"
                  @click="login()"
                >
                  Masuk
                </v-btn>
              </v-col>

              <v-layout justify-center>
                <label>
                  Belum punya akun? Klik 
                  <router-link
                    to="/Register"
                    class="red--text text--darken-4 text-decoration-none"
                  >
                    Daftar
                  </router-link>
                </label>
              </v-layout>
            </v-card-text>
          </v-card> 
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import api from "@/services/http";
export default {
    data: () => ({
      loadingpersen:false,
    TampilkanSandi: false,
    pesan:'',
      credentials:{
        Nama: '',
        password: ''
      },
    return:{
      loading: true
  
    },

        
  }),
  mounted(){
  },

  methods: {
checkFlag() {
      if(this.$menu == undefined) {
        var cek = window.setTimeout(this.checkFlag, 100); /* this checks the flag every 100 milliseconds*/
      } else {
        localStorage.setItem('Menu',JSON.stringify(this.$menu))
        this.$router.push('/')
        clearTimeout(cek)
      }
  },
   async login(){
     this.pesan = ''
      this.loadingpersen = true
      api.post('/login', this.credentials)
        .then(res=>{
          if (res.data.status == false) {
          this.loadingpersen = false
          this.pesan = "Access Denied : Email Atau Password salah"  
          } else {
                localStorage.setItem('token', res.data.token)
                localStorage.setItem('user', JSON.stringify(res.data.user))
                //triger ke appbar via root dan emit
                this.checkFlag();
                this.$root.$emit('menuappbar')
              //   const menu = async () => {
              //   const menulist = await this.$menu
              //   localStorage.setItem('Menu',JSON.stringify(menulist))
              //   console.log('ok :::',menulist);
              // }
                
                // while(this.$menu == undefined) {
                //   console.log('aww')
                //   }
              // const PromiseMenu = new Promise((resolve) => {
              //   setTimeout(resolve, 500, this.$menu);
              // });
              // var PromiseMenu = new Promise((resolve) => {
              //         resolve(this.$menu);
              //   });
              // Promise.all([PromiseMenu]).then((values) => {
              //   console.log('prm',values[0])
              //   // localStorage.setItem('Menu',JSON.stringify(values[0]))
              //   // this.$router.push('/')
              // });
              // var isfinished
              // var timeout = setInterval(function() {
              //   isfinished = false
              //       if(this.$menu.length > 0 ) {
              //           clearInterval(timeout); 
              //           localStorage.setItem('Menu',JSON.stringify(this.$menu))
              //           isfinished = true;
              //       }
              //   }, 100);
                
                
                
            // console.log(res)
            
          }

        })
        .catch(err=>{
          if (err == "Error: Request failed with status code 500") {
            this.loadingpersen = false
            alert('koneksi database bermasalah')
          }
          // alert('email atau password salah')
          // confirm('email atau password salah')
          console.log('email atau password salah')
        })
    },
    
  },
};

</script>

